    align-items: center;
}
.searchContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.navSearchBar {
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    background-color: rgb(240, 242, 245);
    border: 1px solid rgb(240, 242, 245);
    color: rgb(112, 114, 118);
    height: 40px;
    width: 240px;
}
.navSearchBtn {
    background-color: rgb(240, 242, 245);
    border: 1px solid rgb(240, 242, 245);
    color: rgb(112, 114, 118);
    height: 44px;   
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    padding-left: 10px;
}
.navbtn2 {
    background-color: white;
    border: none;
    padding: 10px 45px;
}
.navbtn2:hover {
    background-color: rgb(242, 242, 242);
    cursor: pointer;
    border-radius: 10px;
}
.navbtn3 {
    background-color: rgb(228, 230, 234);
    border: 1px solid rgb(228, 230, 234);
    border-radius: 50px;
    padding: 9px 10px;
    margin-right: 5px;
}
.navbtn3:hover, .navProfilePicContainer:hover {
    background-color: rgb(216, 218, 222);
    border: 1px solid rgb(216, 218, 222);
    cursor: pointer;
}
.navProfilePicContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    border-radius: 50px;
    background-color: rgb(228, 230, 234);
    border: 1px solid rgb(228, 230, 234);
    font-weight: 545;
    padding-left: 2px;
    padding-right: 8px;
}
.navProfilePic {
    background-image: url('./components/images/profile-pic-cropped.jpg');
    background-size: 100% 100%;
    border-radius: 50px;
    margin-right: 5px;
    width: 38px; 
    height: 38px;
}
.navContainer3 {
    display: flex;
    flex-direction: row;
}
.settings-modal-content-top {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(51, 120, 234);
    color: white;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    font-size: 30px;
}
.settings-modal-content-bottom {
    padding: 10px;
}
/* The Modal (background) */
.settingsModal {
    display: none; 
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0,0,0,0.4); 
}
/* Modal Content/Box */
.settings-modal-content {
    background-color: white;
    color: black;
    margin: 15% auto;
    width: 350px; 
    height: 350px;
    border-radius: 12px;
    display: grid;
    grid-template-rows: 50% 50%;
}
/* The Close Button */
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
.close:hover, .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}
.darkmodeName {
    margin: 0px;
}
.darkmodeP {
    margin-top: 0px;
    color: gray
}
.darkmodeBtn {
    background-color: lightgrey;
    border: none;
    padding: 10px 20px 5px 20px;
    border-radius: 5px;
    margin-bottom: 2.5px;
}
.darkmodeBtn:hover {
    background-color: lightgrey;
    border: none;
}
.headerContainer {
